.btn-border {
  position: relative;
  display: inline-block;
}
.btn-border::after {
  content: "";
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  width: 100%;
  height: 88%;
  border: 0.0625rem solid #4e7f85;
  z-index: 0;
}
.btn-border .btn-border__inner {
  position: relative;
  display: inline-block;
  padding: 1rem 5.5rem 1rem 2.5rem;
  background-color: #4e7f85;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  font-weight: 400;
  text-decoration: none;
  line-height: 157.143%;
  z-index: 1;
  overflow: hidden;
  transition: color 0.3s ease;
}
.btn-border .btn-border__inner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-color: #fff;
  z-index: -1;
  transition: width 0.6s ease-in-out;
  border: 0.0625rem solid #4e7f85;
}
.btn-border .btn-border__inner:hover::before {
  width: 100%;
}
.btn-border .btn-border__inner:hover {
  color: #4e7f85;
}
.btn-border .btn-border__inner:hover .btn-border__arrow {
  border-color: #4e7f85;
}
.btn-border .btn-border__arrow {
  position: absolute;
  top: 50%;
  right: 2.75rem;
  transform: translateY(-50%) skew(45deg);
  width: 2.5rem;
  height: 0.375rem;
  border-bottom: 0.0625rem solid #fff;
  border-right: 0.0625rem solid #fff;
  transition: border-color 0.3s ease;
}

