@use "global" as *;

.drawer {
  position: fixed;
  z-index: 101;
  top:0;
  right: 0;
  width: 100%;
  height: 100vh;
  height: 100svh;
  padding-top: rem(80);
  background-color: $green;
  overflow-y: scroll;
  opacity: 0;
  visibility: hidden;
  // translate: 101%;
  // transition: translate 0.5s ease;
  transition: opacity 0.5s ease;

  &.js-show {
    // translate: 0;
    opacity: 1;
    visibility: visible;
  }
}

.drawer__body {
  width: 100%;
  height: fit-content;
  padding: rem(24) rem(37);
  display: flex;
  flex-direction: row;
  gap: rem(29);
}

.drawer__list {
  display: flex;
  flex-direction: column;
  row-gap: rem(40);
}

.drawer__category {
  display: flex;
  flex-direction: column;
  row-gap: rem(24);
}

.drawer__category-title {
  color: $white;
  font-family: $jp-font-family;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 112.5%; /* 112.5% */
}

.drawer__sub-list {
  display: flex;
  flex-direction: column;
  row-gap: rem(24);
}

.drawer__item {
  color: $white;
  font-family: $jp-font-family;
  font-size: rem(16);
  font-weight: $normal;
  line-height: 112.5%; /* 112.5% */
}

